.split-slider {
  position: relative;
  padding-top: 104px;
  padding-bottom: 104px;
  background-color: #f6f6f6;
}

.split-slider__title {
  margin: 0;
  margin-bottom: 50px;
  font-weight: 400;
  font-size: 48px;
  line-height: 56px;
  text-align: center;
}

.split-slider__container {
  position: static;
  width: 1116px;
  margin: 0 auto;
}

.split-slider__wrapper {
  width: 1116px;
  padding-top: 20px;
}

.split-slider__prev, .split-slider__next {
  transition: all 0.2s ease;
  top: 456px;
  width: 64px;
  height: 64px;
  margin-top: 0;
  border-radius: 50%;
  background-color: #e9e9e9;
}

.split-slider__prev:hover, .split-slider__next:hover {
  background-color: #dadada;
}

.split-slider__prev {
  left: 50%;
  margin-left: -704px;
}

.split-slider__next {
  right: 50%;
  margin-right: -704px;
}

.split-slider__prev::after, .split-slider__next::after {
  display: none;
}

.split-slider__prev::before, .split-slider__next::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 8px;
  height: 12px;
  margin-left: -5px;
  margin-top: -6px;
  background-image: url("../img/left.svg");
  background-repeat: no-repeat;
}

.split-slider__next::before {
  margin-left: -3px;
  transform: rotate(180deg);
}

.split-slider__item {
  position: relative;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  box-sizing: border-box;
  width: 215px;
  margin: 0 32px;
  max-width: 215px;
  height: auto;
  min-height: 646px;
  padding-top: 126px;
  padding-bottom: 40px;
  font-weight: 400;
  font-size: 15px;
  line-height: 22px;
  background-repeat: no-repeat;
}

.split-slider__item--hit::before {
  content: "!";
  position: absolute;
  top: 10px;
  right: 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 56px;
  width: 56px;
  padding-left: 12px;
  padding-top: 3px;
  text-align: center;
  font-weight: bold;
  background-color: #ffd43c;
  border-radius: 50%;
  transform: rotate(-15deg);
}

.split-slider__item--hit::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: calc(100% + 50px);
  height: calc(100%);
  margin-left: -25px;
  margin-top: -10px;
  background-image: url("../img/frame.svg");
  background-repeat: no-repeat;
}

.split-slider__item--r32::before {
  content: "R32";
  position: absolute;
